:root {
  font-size: 16px;
  --brandColor: #004687;
  --altBrandColor: #bd9b60;
  --mainWhite: #ffffff;
  --mainBlack: #000000;
}

::-webkit-input-placeholder {
  color: #000000;
  color: var(--mainBlack);
}

::-moz-placeholder {
  color: #000000;
  color: var(--mainBlack);
}

::-ms-input-placeholder {
  color: #000000;
  color: var(--mainBlack);
}

::placeholder {
  color: #000000;
  color: var(--mainBlack);
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
}

p {
  font-family: "Lora", serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
  background: var(--brandColor);
}

.headerText {
  font-size: 3.5rem;
  color: var(--mainWhite);
}

@media only screen and (max-width: 500px) {
  .headerText {
    font-size: 2.3rem;
    text-align: center;
  }
}

.todoItemContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.todoCard {
  background: var(--brandColor);
  width: 300px;
  border-radius: 5px 20px 5px;
  margin: 10px;
  padding: 20px;
  -webkit-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
  -webkit-animation: makeAppear 0.7s ease-in-out;
          animation: makeAppear 0.7s ease-in-out;
}

.todoCard:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  border-radius: 20px 5px 20px;
}

.todoHeader {
  text-transform: capitalize;
  color: var(--mainWhite);
  border: 1px solid var(--altBrand);
  text-align: center;
}

.todoDate,
.todoTime {
  color: var(--mainWhite);
}

.todoButton {
  background: var(--altBrandColor);
  color: var(--mainWhite);
  font-size: 1.2rem;
  border: none;
  padding: 10px;
  margin-right: 5px;
}

.todoButton:hover {
  background: goldenrod;
}

@-webkit-keyframes makeAppear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes makeAppear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.todoForm {
  display: flex;
  flex-direction: column;
  width: 400px;
  margin: 20px auto;
}

.todoLabel {
  font-size: 1.2rem;
  padding: 5px 0;
}

.todoError {
  text-align: center;
  color: red;
}

.todoInput {
  margin: 0 0 10px 0;
  border: 1px solid var(--altBrandColor);
  height: 25px;
  font-size: 1.3rem;
  padding: 2px;
}

.todoButton {
  font-size: 1.3rem;
}

@media only screen and (max-width: 500px) {
  .todoForm {
    width: 80%;
  }
}

